/* Cinzel-bold */
@font-face {
  font-family: 'Cinzel';
  src: url('../fonts/cinzel/Cinzel-Bold.eot');
  src: local('Cinzel'),
  url('../fonts/cinzel/Cinzel-Bold.eot?#iefix') format('embedded-opentype'),
  url('../fonts/cinzel/Cinzel-Bold.woff2') format('woff2'),
  url('../fonts/cinzel/Cinzel-Bold.woff') format('woff'),
  url('../fonts/cinzel/Cinzel-Bold.ttf') format('truetype');
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

/* Inter-Regular*/
@font-face {
  font-family: 'Inter';
  src: url('../fonts/inter/Inter-Regular.eot');
  src: local('Inter Regular'), local('Inter-Regular'),
  url('../fonts/inter/Inter-Regular.eot?#iefix') format('embedded-opentype'),
  url('../fonts/inter/Inter-Regular.woff2') format('woff2'),
  url('../fonts/inter/Inter-Regular.woff') format('woff'),
  url('../fonts/inter/Inter-Regular.ttf') format('truetype');
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
